            <a href="javascript:;" class="btn" @click="handeladd('-')">
              <img src="../assets/img/btn_reduce.png" alt>
            </a>
            <input type="text" v-model="maximineValue" disabled>
            <a href="javascritp:;" class="btn" @click="handeladd('+')">
              <img src="../assets/img/btn_add.png" alt>
            </a>
          </div>
          <div class="player" @click="gameSstart()">
            <img src="../assets/img/btn_yaoyaole_yaoyao.png" alt>
          </div>
        </div>
        <div class="btn-bs" :class="{'active':!flag}"  v-swipedown='swipedown'></div>
      </div>
    </div>
    <div v-show="succesType" class="module_succes animated bounceInDown success">
      <img v-if="requestDate.result==1" src="../assets/img/img_victory.png" alt>
      <img v-else src="../assets/img/img_xinsui.png" alt>
    </div>
