            <li>
              <h2>时间</h2>
              <h2>投注</h2>
              <h2>收益</h2>
              <h2>摇一摇</h2>
            </li>
      
              <li v-if="ranglist.length!=0" v-for="(item, index) in ranglist" :key="index">
              <span>{{item.createdTime | rangItem}}</span>
              <span>{{item.jetton}}</span>
              <span>{{item.award}}</span>
              <span>
                <img v-for="(item, index) in item.panel" :key="index" :src="imgs[item]" alt>
              </span>
            </li>
            
            <li v-if="ranglist.length==0">
                <img src="../assets/img/img_allpages_nodata.png" alt="">
            </li>
            
          </ul>
