</template>

<script>
import { userInfo, getBalance, gameShakeStart, gameShakeBets } from "@/api";
export default {
  name: "yaoyao",

  data() {
    return {
      arrPos: [0, 0, 0], //存放当前位置
      arrNum: [0, 0, 0], //存放随机数
      step: (363 / 2) * 8, //控制转的步数
      total: 6, //奖项个数
      maximineValue: 10,
      balance: 0,
      flag: true,
      succesType: false, //显示胜利
      requestDate: {
        result: 0,
        award: 0
      },
      ranglist: [],
      imgs: [
        require("../assets/img/0.png"),
        require("../assets/img/1.png"),
        require("../assets/img/2.png"),
        require("../assets/img/3.png"),
        require("../assets/img/4.png"),
        require("../assets/img/5.png")
      ]
    };
  },

  components: {
    //模板
  },
  filters: {
    rangItem(val) {
      // console.log(val)
      return val.substring(5, val.indexOf("T"));
    }
  },
  computed: {
    //计算属性
  },

  methods: {
    //方法
    gameSstart() {
      if (!this.flag) {return}

        this.flag = false;
      
      this.vuegetBalance(UserBalance => {
        console.log(this.balance);
        this.balance = UserBalance;
        if (Number(UserBalance) < Number(this.maximineValue)) {
          // alert("用户余额不足");
          this._reactNative("1")
          this.flag =true;
          return;
        }
      });
      
      
      gameShakeStart({ jetton: this.maximineValue }).then(res => {
        console.log(res);
        if (res.status == 200 && res.data.code == 0) {
          let { result, award } = res.data.data;
          this.requestDate.result = result;
          this.requestDate.award = award;
          let tempArr = this.arrNum;
          // this.arrNum = this._getNumb(this.total);
          this.arrNum = res.data.data.panle;
          this.arrPos = this.arrPos.map((value, i) => {
            return (value +=
              -this.step -
              (this.arrNum[i] - tempArr[i] <= 0
                ? this.arrNum[i] - tempArr[i] + this.total
                : this.arrNum[i] - tempArr[i]) *
                (121 / 2));
          });
        } else {
          
        }
      });
    },
    swipedown(e){
      console.log(e);
      this.gameSstart()
    },
    handeladd(val) {
      if (this.balance <= 0) {
        //余额不足
        this._reactNative("1");
      }
      if (val === "+") {
        this.maximineValue++;
      } else {
        this.maximineValue - 1 <= 0 ? this.maximineValue : this.maximineValue--;
      }
    },
    modalbox() {
      // 弹框方案

      // console.log(htmlRef);

      // window.layer.open({
      //   content: htmlRef,
      //   className: "layui-poker-box",
      //   shadeClose: false
      // });
      gameShakeBets({ row: 5 }).then(res => {
        console.log(res);
        if (res.status == 200 && res.data.code == 0) {
          this.ranglist = res.data.data;
      
          this.$nextTick(() => {
            // let htmlRef =
            window.layer.open({
              content: this._domToStirng(this.$refs.pokerbox),
              className: "layui-poker-box",
              shadeClose: false
            });
          });
        }else if(res.data.code ==1006){
          this.ranglist = [];
          window.layer.open({
              content: this._domToStirng(this.$refs.pokerbox),
              className: "layui-poker-box",
              shadeClose: false
            });
        }
      });
    },
    endtransition(index) {
      console.log(index)
      if (index == 2) {
         this.flag = true;
        // alert(1)
        // alert("出结果了");
        this.vuegetBalance()
        this.succesType = true;
        if (this.succesType) {
          setTimeout(() => {
            
            this.succesType = false;
          }, 3000);
        }
      }
    },
    handelClickLeave() {
      this._reactNative("0");
    },
    vuegetBalance(callback) {
      let _lesf = this;
      getBalance().then(res => {
        if (res.status == 200) {
          console.log(res);
          let UserBalance =Number( res.data.data.balance);
            if(UserBalance==0){
              this._reactNative("1")
            }
          this.$nextTick(function() {
            _lesf.balance = Number(UserBalance);
          });
          if (callback) {
            callback(UserBalance);
          }
          // this.set(this.data,this.balance,res.data.data.balance)
        } else {
          alert(res.data.msg);
        }
      });
    },
    _domToStirng(htmlDOM) {
      // if (outerHTML in htmlDOM) {
      //   return htmlDOM.outerHTML;
      // } else {
      var div = document.createElement("div");

      div.appendChild(htmlDOM);

      return div.innerHTML;
      // }
    },
    _getNumb(n) {
      var num = new Array();
      var val;
      for (var i = 0; i < 3; i++) {
        val = Math.floor(Math.random() * n);
        num[num.length] = val;
      }
      return num;
    },
    _reactNative(val) {
      try {
        switch (val) {
          case "0": // 离开房间
            window.ReactNativeWebView.postMessage("0");
            break;
          case "1": // 充值弹窗
            window.ReactNativeWebView.postMessage("1");
            break;
          case "2": // 震动
            window.ReactNativeWebView.postMessage("2");
            break;

          default:
            break;
        }
      } catch (error) {
        console.error(error);
        return;
      }
    }
  },
  //beforeCreate:function(){
  //     console.log('1-beforeCreate 初始化之后');
  // },
  created: function() {
    // console.log("2-created 创建完成");
    this.vuegetBalance();
  }
  // beforeMount:function(){
  //     console.log('3-beforeMount 挂载之前');
  // },
  // mounted:function(){
  //     console.log('4-mounted 被创建');
  // },
  // beforeUpdate:function(){
  //     console.log('5-beforeUpdate 数据更新前');
  // },
  // updated:function(){
  //     console.log('6-updated 被更新后');
  // },
  // activated:function(){
  //     console.log('7-activated');
  // },
  // deactivated:function(){
  //     console.log('8-deactivated');
  // },
  // beforeDestroy:function(){
  //     console.log('9-beforeDestroy 销毁之前');
  // },
  // destroyed:function(){
  //     console.log('10-destroyed 销毁之后')
  // }
};
</script>
